// @flow
import React, { Component } from 'react';
import { Provider } from 'react-redux';

import store from './store';
import './App.css';
import Router from'./Router'

class App extends Component {
  render() {
    return (
        <Provider store={store}>
            <Router />
        </Provider>
    );
  }
}

export default App;





